html,
body {
    height: 100%;
    width: 100%;
}

body {
    @include serif-font;
}

hr {
    border-color: $theme-primary;
    border-width: 3px;
    max-width: 900px;
}

hr.light {
    border-color: white;
}

a {
    @include transition-all;
    color: $theme-primary;
    &:hover,
    &:focus {
        color: #0a3d50
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    @include sans-serif-font;
}

p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;
}

.bg-primary {
    background-color: $theme-primary;
}

.bg-dark {
    background-color: $theme-dark;
    color: #0a3d50;
}

.text-faded {
    color: rgba(white, .7);
}

section {
    padding: 100px 0;
}

aside {
    padding: 50px 0;
}

.no-padding {
    padding: 0;
}

/* Body and main container styling */


body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
}

.main-content {
    max-width: 900px; /* Control the width for better readability */
    margin: 0 auto; /* Center the content */
    padding: 40px 20px; /* Add padding to the sides */
    font-family: 'Arial', sans-serif;
    color: #333; /* Dark gray for better contrast */
    line-height: 1.8; /* Improve line spacing for better readability */
}

/* Image Styling (Ensuring full-width and centered) */
.main-content img {
    width: 100%;  /* Full width of the container */
    height: auto;   /* Maintain aspect ratio */
    display: block; /* Remove the bottom space */
    margin: 0 auto; /* Center the image horizontally */
    padding: 0;     /* Remove any padding */
    object-fit: cover; /* Ensure the image covers the space, if needed */
}

/* Headings */
.main-content h1,
.main-content h2,
.main-content h3,
.main-content h4,
.main-content h5,
.main-content h6 {
    font-family: 'Georgia', serif; /* Serif font for headings */
    color: #0a3d50; /* A dark blue color for headings */
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: 700;
}

/* Heading 1 */
.main-content h1 {
    font-size: 2.5em;
}

/* Heading 2 */
.main-content h2 {
    font-size: 2em;
}

/* Heading 3 */
.main-content h3 {
    font-size: 1.5em;
}

/* Paragraphs */
.main-content p {
    font-size: 16px;
    margin-bottom: 20px;
    color: #666; /* Light gray for paragraphs */
    text-align: justify;
}

/* Lists */
.main-content ul,
.main-content ol {
    padding-left: 20px;
    margin-bottom: 20px;
    text-align: justify;
}

.main-content li {
    font-size: 16px;
    line-height: 1.6;
    text-align: justify;
}

/* Blockquotes */
.main-content blockquote {
    font-style: italic;
    padding: 15px;
    background-color: #f4f4f4;
    border-left: 5px solid #29a5c5;
    margin: 20px 0;
    font-size: 18px;
    color: #555;
}

/* Links */
.main-content a {
    color: #29a5c5; /* Use your primary theme color */
    text-decoration: none;
    font-weight: bold;
    transition: color 0.3s ease;
}

.main-content a:hover,
.main-content a:focus {
    color: #0a3d50; /* Darken on hover */
}

/* Code Blocks */
.main-content pre,
.main-content code {
    font-family: 'Courier New', monospace;
    background-color: #f7f7f7;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
    font-size: 14px;
    text-align: justify;
}

.main-content pre {
    white-space: pre-wrap; /* Allow code to wrap */
    word-wrap: break-word; /* Handle long code lines */
}

/* Inline Code */
.main-content code {
    padding: 3px 6px;
    background-color: #eef0f4;
    border-radius: 5px;
    color: #d6336c; /* Use a distinct color */
}

/* Tables */
.main-content table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
}

.main-content table,
.main-content th,
.main-content td {
    border: 1px solid #ddd;
}

.main-content th,
.main-content td {
    padding: 10px;
    text-align: left;
}

/* Horizontal Rules */
.main-content hr {
    border: 0;
    height: 3px;
    width: 100%; /* Adjust width */
    background-color: #29a5c5;
    margin: 40px 0;
}

@media (max-width: 768px) {
    .main-content {
        padding: 20px 10px;
    }

    .main-content h1 {
        font-size: 2em;
    }

    .main-content h2 {
        font-size: 1.75em;
    }

    .main-content h3 {
        font-size: 1.25em;
    }

    .main-content p {
        font-size: 15px;
    }

    .main-content pre {
        font-size: 12px;
    }
}


// Navigation

.navbar-default {
    background-color: #29a5c5;
    border-color: rgba($theme-dark, .5);
    @include sans-serif-font;
    @include transition-all;
    .navbar-header .navbar-brand {
        color: $theme-primary;
        @include sans-serif-font;
        font-weight: 700;
        text-transform: none;
        &:hover,
        &:focus {
            color: #0a3d50;
        }
    }
    .nav {
        > li {
            > a,
            > a:focus {
                text-transform: none;
                font-weight: 700;
                font-size: 13px;
                color: $theme-dark;
                &:hover {
                    color: $theme-primary;
                }
            }
            &.active {
                > a,
                > a:focus {
                    color: $theme-primary !important;
                    background-color: #29a5c5;
                    &:hover {
                        background-color: #0a3d50;
                    }
                }
            }
        }
    }
    @media (min-width: 768px) {
        background-color: #29a5c5;
        border-color: rgba(#0a3d50, .3);
        .navbar-header .navbar-brand {
            color: rgba(white, .7);
            &:hover,
            &:focus {
                color: white;
            }
        }
        .nav > li > a,
        .nav > li > a:focus {
            color: rgba(white, .7);
            &:hover {
                color: white;
            }
        }
        &.affix {
            background-color: white;
            border-color: rgba($theme-dark, .5);
            .navbar-header .navbar-brand {
                color: $theme-primary;
                font-size: 14px;
                &:hover,
                &:focus {
                    color: #0a3d50;
                }
            }
            .nav > li > a,
            .nav > li > a:focus {
                color: $theme-dark;
                &:hover {
                    color: $theme-primary;
                }
            }
        }
    }
}

// Homepage Header

header {
    position: relative;
    width: 100%;
    min-height: auto;
    @include background-cover;
    background-position: center;
    background-image: url('../img/ATSheadernew2.jpeg');
    text-align: center;
    color: white;
    .header-content {
        position: relative;
        text-align: center;
        padding: 100px 15px 100px;
        width: 100%;
        .header-content-inner {
            h1 {
                font-weight: 700;
                text-transform: uppercase;
                margin-top: 0;
                margin-bottom: 0;
            }
            hr {
                margin: 30px auto;
            }
            p {
                font-weight: 300;
                color: rgba(white, .7);
                font-size: 16px;
                margin-bottom: 50px;
            }
        }
    }
    @media (min-width: 768px) {
        min-height: 100%;
        .header-content {
            position: absolute;
            top: 50%;
            -webkit-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
            padding: 0 50px;
            .header-content-inner {
                max-width: 1000px;
                margin-left: auto;
                margin-right: auto;
                p {
                    font-size: 18px;
                    max-width: 80%;
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
    }
}

// Sections

.section-heading {
    margin-top: 0;
}

.service-box {
    max-width: 400px;
    margin: 50px auto 0;
    @media (min-width: 992px) {
        margin: 20px auto 0;
    }
    p {
        margin-bottom: 0;
    }
}

.portfolio-box {
    position: relative;
    display: block;
    max-width: 650px;
    margin: 0 auto;
    .portfolio-box-caption {
        color: white;
        opacity: 0;
        display: block;
        background: rgba( $theme-primary, .9 );
        position: absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
        height: 100%;
        @include transition-all;
        .portfolio-box-caption-content {
            width: 100%;
            text-align: center;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            .project-category,
            .project-name {
                @include sans-serif-font;
                padding: 0 15px;
            }
            .project-category {
                text-transform: uppercase;
                font-weight: 600;
                font-size: 14px;
            }
            .project-name {
                font-size: 18px;
            }
        }
    }
    &:hover {
        .portfolio-box-caption {
            opacity: 1;
        }
    }
    @media (min-width: 768px) {
        .portfolio-box-caption {
            .portfolio-box-caption-content {
                .project-category {
                    font-size: 16px;
                }
                .project-name {
                    font-size: 22px;
                }
            }
        }
    }
}

.call-to-action {
    h2 {
        margin: 0 auto 20px;
    }
}

// Bootstrap Overrides
.text-primary {
    color: $theme-primary;
}

.no-gutter > [class*='col-'] {
    padding-right:0;
    padding-left:0;
}

// Button Styles
.btn-default {
    @include button-variant($theme-dark, white, white);
}

.btn-primary {
    @include button-variant(white, $theme-primary, $theme-primary);
}

.btn {
    @include sans-serif-font;
    border: none;
    border-radius: 300px;
    font-weight: 700;
    text-transform: uppercase;
}

.btn-xl {
    padding: 15px 30px;
}

// Contact
#contact .fa {
    color: $theme-dark;
    font-size: 4em;
}

// Extras
// -- Highlight Color Customization
::-moz-selection {
    color: white;
    text-shadow: none;
    background: $theme-dark;
}

::selection {
    color: white;
    text-shadow: none;
    background: $theme-dark;
}

img::selection {
    color: white;
    background: transparent;
}

img::-moz-selection {
    color: white;
    background: transparent;
}

body {
    -webkit-tap-highlight-color: $theme-dark;
}

